html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --font-inter: "Inter", "sans-serif";
}

.card {
  padding: 1.875rem;
}

.toggle __button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle-light);
}